@import url(https://fonts.googleapis.com/css2?family=Ranga&display=swap);
@font-face {
  font-family: "mexcellent3d";
  src: url(/channy-portfolio.github.io/static/media/mexcellent3d.de80edcb.otf);
}

* {
  font-family: "consolas";
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.home {
  height: 100vh;
  text-align: center;
  background-color: #463d3d;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.home::before {
  content: "";
  position: absolute;
  top: 0%;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.home::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(/channy-portfolio.github.io/static/media/background-home-sakura.7a56e2dd.png);
  background-repeat: no-repeat;
  opacity: 0.5;
  top: 0;
  left: 0;
}

.intro {
  z-index: 5;
}

h1,
hr {
  margin: 0.5rem;
}

h1 {
  color: #eae5b9;
  font-size: 50px;
}

h2 {
  color: white;
  font-size: 50px;
  margin: 1rem 0 2rem;
}

hr {
  border: 1px solid #eabfb9;
}

button {
  all: unset;
  background-color: #eae5b9;
  font-size: 24px;
  padding: 1rem;
  width: 10rem;
  border-radius: 4px;
  margin: 2rem 1rem 0;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 1s;
}

button:hover {
  background-color: #eabfb9;
  color: white;
}

button:before {
  content: "";
  width: 100%;
  height: 50%;
  background-color: white;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translateX(-100%) rotate(45deg);
          transform: translateX(-100%) rotate(45deg);
  transition: all 1s;
}

button:hover:before {
  -webkit-transform: translateX(100%) rotate(45deg);
          transform: translateX(100%) rotate(45deg);
}

.aboutMe {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: #cebea5;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
  font-size: 24px;
  position: relative;
  z-index: -10;
  padding: 0 0 6rem;
}

.aboutMe::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, transparent, #463d3d);
  z-index: -8;
}

.aboutMe::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  top: 0;
  left: 0;
}

.aboutMe h3 {
  font-size: 50px;
  text-align: center;
  z-index: 10;
  letter-spacing: 15px;
  color: white;
}

.aboutMeContainer {
  margin-top: 3rem;
  width: 70%;
  z-index: 10;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 2rem;
}

.itemContainer {
  height: 100%;
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
  color: white;
}

.imageContainer {
  background-color: #ffffff6c;
  box-shadow: 0 4px 4px 0 #eb988c;
  height: 26.5rem;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 5rem;
}

#myPicture {
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}

pre {
  font-size: 35px;
}

.skills,
.imageContainer {
  display: inline-block;
}

.skills {
  margin: 10rem 0 0;
  width: 100%;
  border-radius: 0 0 2rem 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.skill {
  margin: 0 0 1rem;
  padding: 1rem 0 1rem 1rem;
  width: 85%;
  height: 2.5rem;
  line-height: 2.5rem;
  background-color: #eb988c;
  font-size: 35px;
  position: relative;
  border-radius: 0.5rem;
  align-self: center;
  justify-self: center;
}

.skill:last-child {
  margin-bottom: 3rem;
}

.skill::before {
  content: "";
  padding: 1rem 0 1rem 1rem;
  height: 2.5rem;
  width: 0;
  line-height: 2.5rem;
  position: absolute;
  top: 0;
  left: 0;
  font-family: inherit;
  z-index: 20;
}

.html::before {
  content: "HTML";
  background-color: #eabfb9;
  width: calc(90% - 2rem);
  z-index: 50;
}

.css::before {
  content: "CSS";
  background-color: #eabfb9;
  width: calc(85% - 2rem);
  z-index: 50;
}

.js::before {
  content: "JavaScript && ES6";
  background-color: #eabfb9;
  width: calc(80% - 2rem);
  z-index: 50;
}

.reactjs::before {
  content: "ReactJS";
  background-color: #eabfb9;
  width: calc(75% - 2rem);
  z-index: 50;
}

.git::before {
  content: "Git";
  background-color: #eabfb9;
  width: calc(85% - 2rem);
  z-index: 50;
}

#pinkcloud {
  position: absolute;
  opacity: 0.5;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  left: 10rem;
  top: -5rem;
  /* width: 25rem; */
}

#moon {
  position: absolute;
  right: 10rem;
  top: -5rem;
  opacity: 0.5;
  -webkit-filter: blur(8px);
          filter: blur(8px);
  width: 25rem;
}

.projects {
  background-color: #cebea5;
  height: 100vh;
  position: relative;
  background-image: linear-gradient(
      to bottom,
      transparent,
      transparent,
      #edcbce,
      #edcbce,
      #edcbce
    ),
    url(/channy-portfolio.github.io/static/media/background-pinkmountain2.384395f7.png);
  background-repeat: no-repeat;
  background-position-y: 25rem;
  background-size: cover;
}

.projects h3 {
  font-size: 50px;
  text-align: center;
  z-index: 10;
  letter-spacing: 15px;
  color: white;
}

.project-container {
  margin-top: 10rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.projects .project {
  box-sizing: border-box;
}

.projects .project {
  height: 30rem;
  padding: 2rem 0;
  width: 35rem;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
}

.projects .project:nth-of-type(1),
.projects .project:nth-of-type(3) {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}

.image-container {
  width: 85%;
  height: 20rem;
  background-color: #463d3d;
  border-radius: 0.75rem;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.projects .project img {
  width: 100%;
  object-fit: scale-down;
}

.projects .project p {
  font-size: 24px;
  margin-top: 1rem;
}

.contactMe {
  height: 100vh;
  text-align: center;
  background: linear-gradient(to top, #463d3d, #edcbce);
  overflow: hidden;
  position: relative;
}

.contactMe::before {
  content: "";
  position: absolute;
  background-image: url(/channy-portfolio.github.io/static/media/background-undersea.e32ace33.png);
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  background-position: left bottom;
  top: 0;
  left: 0;
}

.form-container {
  width: 32rem;
  z-index: 10;
  background-image: linear-gradient(
      to right bottom,
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.3)
    ),
    url(/channy-portfolio.github.io/static/media/background-sea.17fa0974.png);
  background-repeat: no-repeat;
  background-position: left center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10rem auto;
  padding: 2rem;
  box-shadow: 0px -4px 4px 0 #eb988c;
}

form {
  width: 100%;
}

.form-container input,
.form-container textarea,
.form-container button {
  display: block;
  width: 100%;
  margin: 1rem 0;
  padding: 1rem 0;
  font-size: 24px;
  outline: none;
}

.side-contact {
  position: fixed;
  top: 2rem;
  right: 0;
  padding: 2rem 1rem;
  width: 200px;
  background-color: #ffffff6c;
  box-shadow: 0 -4px 4px 0 #eb988c;
  z-index: 100;
}

li {
  list-style: none;
  color: #eb988c;
  font-family: "Ranga", cursive;
  font-size: 24px;
  letter-spacing: 5px;
}

.links,
.links:visited {
  text-decoration: none;
  color: inherit;
}

nav {
  position: fixed;
  bottom: 2rem;
  right: 0;
  padding: 2rem 1rem;
  height: 300px;
  width: 200px;
  background-color: #ffffff6c;
  box-shadow: 0 4px 4px 0 #eb988c;
  text-align: right;
  z-index: 100;
}

nav li {
  font-size: 40px;
  line-height: 5rem;
  cursor: pointer;
  transition: 1s all;
}

nav li:hover {
  color: #fff;
}

.cursor {
  width: 3rem;
  height: 3rem;
  border: 2px solid #eb988c;
  border-radius: 50%;
  box-shadow: 0 0 4px 4px #eb988c;
  background-color: #ffffff6c;
  position: absolute;
  /* transform: translate(-50%, -50%); */
  visibility: hidden;
  z-index: 50;
}

img.movingImages {
  position: absolute;
  /* z-index: 100; */
  width: 50vw;
  top: -20%;
  left: 25%;
}

